	module.exports = {"data":{"post":{"html":"<p><a href=\"http://www.dansanderson.com/blogtracker/\">blogtracker</a> now lets you search for blogs by name or URL, and even lets you set a time zone. Cool. </p>\n<p>I've been inspired to overhaul my favorites list here. It's mostly for my own use, but everyone is free to click on the links.</p>\n<p>Keeping Christopher up for an extra hour worked, but not as well as we had hoped. He slept one hour longer, but that hour was of course negated by the switch back to standard time.</p>\n<p>weblogs.com seems to have stumbled over the time change... the 'when' attributes was showing negative numbers for new updates in the first hour after the switch. The problem disappeared after that \";->\"</p>\n<p>We finally got confirmation from EWETel that we will be switching our phone line on 23 Nov. That's good, since our flatrate with T-Online expired two weeks ago. Until EWETel takes over, we're paying for our Internet by the minute, just like in the dark ages.</p>\n<p><a href=\"http://www.nypost.com/sports/worldseries/34488.htm\">NY Post</a>: \"The Yankees melted in the desert last night in Game 1 of the World Series. They were putrid on the mound, rotten in the field and impotent at the plate.\" <i>(via <a href=\"http://scriptingnews.userland.com/backissues/2001/10/28#l0d7ab9c1ed29596ac4000ab08ee2eeb8\">Scripting News</a>, no friend of the Yankees)</i></p>","fields":{"title":"Moonlight Savings Time","date":"28 Oct 2001","url":"https://papascott.de/2001-10-28-moonlight-savings-time/"}}},"pathContext":{"slug":"/2001-10-28-moonlight-savings-time/","prev":{"fields":{"title":"Space Park","date":"27 Oct 2001","slug":"/2001-10-27-space-park/"}},"next":{"fields":{"title":"It Takes Two","date":"29 Oct 2001","slug":"/2001-10-29-it-takes-two/"}}}}

